.results-container {
    display: flex;
    flex-direction: column;
}

.result {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 50px;
    max-width: 550px;
    width: 100%;
    padding: 10px;
    margin-top: 8px;
    text-align: center;
    font-size: 1.3rem;
    vertical-align: middle;
    color: var(--light-color);
    background:#16c5d8;
    border-radius: 25px;
}

.littleCountry {
    align-self: flex-start;
    margin: 0 20px;
}

.littleCountry path {
    fill: var(--light-color) !important;
}

.skipped {
    background-color: rgb(255, 12, 12);
}

.correct {
    background-color: rgb(19, 255, 19);
}

@keyframes elastic {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }
  
    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translate3d(-10px, 0, 0);
    }
  
    20%,
    40%,
    60%,
    80% {
      transform: translate3d(10px, 0, 0);
    }
  }
  

.hundred {
    font-size: 5em;
    animation: elastic 4s infinite;
    text-shadow: #333333 2px;
    margin: 0;
}

.score > h1 {
    text-decoration: underline;
}

@media screen and (max-width: 750px) {
    
    .result {
        font-size: 0.5em;
    }
}