/* шрифты */
@font-face {
    font-family: 'Ubuntu';
    src: url('../fonts/Ubuntu-Bold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Ubuntu';
    src: url('../fonts/Ubuntu-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Ubuntu';
    src: url('../fonts/Ubuntu-Medium.woff2') format('woff2');
    font-weight: medium;
    font-style: normal;
}

/* переменные */
:root {
    --spacing_small: 0.625em;
    --spacing_medium: 2rem;
    --spacing_large: 1.875rem;
    --margin-main: 0.9375rem;

    --border-radius: 0.625rem;

    --primary-color: rgb(139, 9, 199);
    --button-color: rgb(90, 0, 192);
    --secondary-color: #00485c;
    --light-color: rgb(238, 238, 238);
    --dark-color: rgb(15, 15, 15);
}

/* базовые стили */
* {
    box-sizing: border-box;
}

body {
    margin: 0 auto;
    width: auto;
    font-family: 'Ubuntu', 'Verdana', 'sans-serif';
    font-size: 1.5rem;
    font-weight: 500;
    letter-spacing: .16rem;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url('../static/bg.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(10px);
    overflow-x: hidden;
}

/* @media (max-width: 408px) {
    body {
        font-size: .5rem !important;
    }
} */

.app {
    min-height: 100vh;
    padding: 0;
}

#main {
    min-height: 100vh;
}

body {
    min-height: 100vh;
    height: auto;
}

form {
    display: block;
}

.toprow {
    display: flex;
    justify-content: space-between;
}

.drawbox {
    background: var(--light-color);
    display: flex;
    /* height: 70vh; */
    border: 2px solid var(--secondary-color);
    border-radius: 25px;
    margin: 2vh 0;
    padding: 20px;
    min-height: 31rem;
    justify-content: center;
    align-items: center;
}

.drawbox>* {
    margin: auto;
}

/* 
.drawbox svg{
    width: 100% !important;
    height: 100% !important;
}
 */

.message {
    display: flex;
    align-items: center;
    background-color: var(--primary-color);
    border: 4px solid var(--secondary-color);
    border-radius: 25px;
    height: auto;
    padding: var(--spacing_small);
    color: var(--light-color);
    /* transform: rotate(-10deg); */
    box-shadow: 7px 7px 30px 0px rgba(0, 0, 0, 0.41);
    /* transition: ease 1s; */
    font-size: 1rem;
    max-width: 935px;
}

button.button-back {
    display: block;
    margin: 1em auto !important;
}

.text-input {
    width: 30vw;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 700;
    -webkit-appearance: textfield;
    display: inline-block;
    padding: 15px 20px 15px 20px;
    border-style: solid none solid solid;
    border-width: 2px;
    border-color: var(--secondary-color) transparent var(--secondary-color) var(--secondary-color);
    border-radius: 25px 25px 25px 25px;
    box-shadow: inset 0 0 0 0 #fff;
}

.text-input:focus {
    outline: 0;
    border-color: var(--primary-color);
}

#back-button {

    /* margin-left: -27px; */
}

#back-button:hover {
    /* margin-left: calc(-1*var(--margin-main)); */
    background-color: var(--secondary-color);
    border-color: var(--primary-color);
    color: var(--light-color);
}

.button {
    display: inline-block;
    padding: 14px 40px 12px;
    border: 2px solid var(--secondary-color);
    background-color: var(--primary-color);
    transition: background-color 180ms ease, color 180ms ease, border-color 180ms ease;
    font-family: sans-serif;
    color: var(--light-color);
    border-radius: 25px;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    outline: 0;
    transition: ease 0.2s;
    -webkit-tap-highlight-color: #005baa;
}


.button-right {
    background-color: var(--primary-color);
    /* border-right: 2px solid var(--secondary-color); */
    border-radius: 100px 100px 100px 100px;
    margin-bottom: var(--margin-main);
}

.button-middle {
    background-color: mediumpurple;
}

.button:hover {
    border: 2px solid var(--secondary-color);
    background-color: var(--light-color);
    color: var(--secondary-color);
}

.button:active {
    outline: 0;
    border-color: var(--secondary-color);
    background-color: var(--secondary-color);
    color: var(--light-color);
}

.button[disabled] {
    background-color: var(--primary-color);
    color: var(--light-color);
    border: 2px solid var(--secondary-color);
    cursor: not-allowed;
}

.button[disabled]:hover,
.button[disabled]:active {
    background-color: var(--primary-color);
    cursor: not-allowed;
    color: var(--light-color);
}

#start-button {
    background-color: var(--primary-color);
    /* padding: var(--spacing_small); */
    border-radius: 25px;
    color: var(--light-color);
}

@media screen and (max-width: 750px) {
    .message {}

    .drawbox {
        min-height: 50vh;
        height: 100%;
    }

    .message>h2 {
        font-size: 0.9em;
    }

    #start-button {
        width: 100px;
    }

    #back-button:hover,
    #back-button:active {
        /* margin-left: -4vw; */
    }

    .text-input {
        width: 100%;
        border: 2px solid var(--button-color);
        border-radius: 25px;
    }

    form {
        padding: 0 0.5em;
    }

    form>.button {
        width: 100%;
        border: 2px solid var(--button-color);
        padding: 10px;
        margin: 0.25em 0;
        border-radius: 10px;
    }

    form>* {
        border: 2px solid #76dbd1;
        border-radius: 10px;
    }

    .button[active]:hover {
        border: 2px solid white;
        margin-top: 2px;
    }

    path,
    line,
    ellipse {
        stroke-width: 2;
    }
}


/* SVG */
path,
line,
ellipse {
    fill: none;
    stroke: #000;
    vector-effect: non-scaling-stroke;
    stroke-width: 3;
}