.results-container {
    display: flex;
    flex-direction: column;
}

.result {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 50px;
    max-width: 550px;
    width: 100%;
    padding: 10px;
    margin-top: 8px;
    text-align: center;
    font-size: 1.3rem;
    vertical-align: middle;
    color: var(--light-color);
    background:#16c5d8;
    border-radius: 25px;
}

.littleCountry {
    align-self: flex-start;
    margin: 0 20px;
}

.littleCountry path {
    fill: var(--light-color) !important;
}

.skipped {
    background-color: rgb(255, 12, 12);
}

.correct {
    background-color: rgb(19, 255, 19);
}

@-webkit-keyframes elastic {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }
  
    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translate3d(-10px, 0, 0);
    }
  
    20%,
    40%,
    60%,
    80% {
      transform: translate3d(10px, 0, 0);
    }
  }

@keyframes elastic {
    from,
    to {
      transform: translate3d(0, 0, 0);
    }
  
    10%,
    30%,
    50%,
    70%,
    90% {
      transform: translate3d(-10px, 0, 0);
    }
  
    20%,
    40%,
    60%,
    80% {
      transform: translate3d(10px, 0, 0);
    }
  }
  

.hundred {
    font-size: 5em;
    -webkit-animation: elastic 4s infinite;
            animation: elastic 4s infinite;
    text-shadow: #333333 2px;
    margin: 0;
}

.score > h1 {
    text-decoration: underline;
}

@media screen and (max-width: 750px) {
    
    .result {
        font-size: 0.5em;
    }
}
.intro {
  /* border: 3px solid var(--primary-color); */
  text-align: center;
  padding: 12px;
  border-radius: var(--border-radius);
  box-shadow: 3px 3px 10px 2px #555151;
  background: linear-gradient(to left, var(--primary-color) 0%, var(--secondary-color) 100%);
}
.name_wrap {
  background-color: rgba(240, 248, 255, 0.233);
  border-radius: var(--border-radius);
}

.animate-charcter {
  text-transform: uppercase;
  background-image: linear-gradient(-225deg,
      #8462ff 0%,
      #9d42ff 15%,
      #ff1361 40%,
      #fff800 60%,
      #ff1361 70%,
      #9d42ff 80%,
      #8462ff 100%);
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: textclip 3s linear infinite;
          animation: textclip 3s linear infinite;
  display: inline-block;
  font-size:  3em;
  background-color: rgba(240, 248, 255, 0.233);
  border-radius: var(--border-radius);
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

