.intro-text {
  font-size: 0.75em;
  font-weight: 100;
  color: var(--light-color);
  background-color: rgba(240, 248, 255, 0.233);
  border-radius: var(--border-radius);
  padding: var(--spacing_small);
}

.select {
  outline: 0;
  transition: ease 0.2s;
  margin: 5px;
  font-size: 2em;
  border-radius: 4px;
  text-decoration: underline;
  font-weight: bold;
  flex-grow: 1;
  color: var(--light-color);
}

.amount-container {
  display: flex;
  flex-wrap: nowrap !important;
}

.amount-container>* {
  padding: 10px;
  flex: 1 1 100%;
}

.amount {
  flex-basis: auto;
  background: var(--secondary-color);
  border: 0;
  text-decoration: none;
  height: 100px;
  flex-shrink: 2;
  box-shadow: 0px 0px 5px var(--primary-color);
}

.continents-container {
  display: flex;
  flex-wrap: wrap;
}

.continent {
  width: 20vw;
  height: 20vw;
  min-width: 150px;
  min-height: 150px;
  border: 3px solid var(--primary-color);
  background-color: var(--primary-color);
  text-decoration: none;
}

.select:hover,
.select:active {
  background-color: var(--secondary-color);
  border: 3px solid var(--secondary-color);
  box-shadow: 0 0 10px var(--secondary-color);
  color: var(--light-color);
  transform: scale(1.002);
}

.continent:focus {
  outline: 0;
  background-color: #57a99a;
  -webkit-animation: shrink 0.25s;
          animation: shrink 0.25s;
}
.select__title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: var(--dark-color);
  background-color: rgba(240, 248, 255, 0.233);
  margin: 0;
  padding: 0.67em 0;
  font-weight: bold !important;
}

.privacy {
  display: flex;
  align-self: center;
  justify-content: center;
  /* width: 100%; */
  grid-gap: 20px;
  gap: 20px;
  /* background-color: rgba(0, 53, 100, 0.233); */
  margin: 1em 5px 5px;
}

.privacy a {
  padding: var(--spacing_medium) var(--spacing_small);
  padding: 0.5em;
  text-align: center;
  background-color: var(--primary-color);
  border: 3px solid var(--primary-color);
  color: var(--light-color);
  border-radius: var(--border-radius);
  text-decoration: none;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
  box-shadow: 0 0 10px var(--primary-color);
}

.privacy a:hover {
  border: 3px solid var(--secondary-color);
  background-color: var(--secondary-color);
  box-shadow: 0 0 10px var(--secondary-color);
}

@-webkit-keyframes shrink {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes shrink {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1);
  }
}

.amount:focus, .amount:hover {
  outline: 0;
  background-color: var(--primary-color);
  color: var(--light-color);
  transform: scale(.9);
  border-color: var(--secondary-color);
  box-shadow: 0px 0px 5px var(--secondary-color);
}
.app {
  margin: 0;
