/* шрифты */
@font-face {
    font-family: 'Ubuntu';
    src: url(/static/media/Ubuntu-Bold.c8708fb1.woff2) format('woff2');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Ubuntu';
    src: url(/static/media/Ubuntu-Regular.3303751b.woff2) format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Ubuntu';
    src: url(/static/media/Ubuntu-Medium.bdd8b7a4.woff2) format('woff2');
    font-weight: medium;
    font-style: normal;
}

/* переменные */
:root {
    --spacing_small: 0.625em;
    --spacing_medium: 2rem;
    --spacing_large: 1.875rem;
    --margin-main: 0.9375rem;

    --border-radius: 0.625rem;

    --primary-color: rgb(139, 9, 199);
    --button-color: rgb(90, 0, 192);
    --secondary-color: #00485c;
    --light-color: rgb(238, 238, 238);
    --dark-color: rgb(15, 15, 15);
}

/* базовые стили */
* {
    box-sizing: border-box;
}

body {
    margin: 0 auto;
    width: auto;
    font-family: 'Ubuntu', 'Verdana', 'sans-serif';
    font-size: 1.5rem;
    font-weight: 500;
    letter-spacing: .16rem;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(/static/media/bg.4c8e826e.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(10px);
    overflow-x: hidden;
}

