.app {
    min-height: 100vh;
    padding: 0;
}

#main {
    min-height: 100vh;
}

body {
    min-height: 100vh;
    height: auto;
}

form {
    display: block;
}

.toprow {
    display: flex;
    justify-content: space-between;
}

.drawbox {
    background: rgb(238, 238, 238);
    background: var(--light-color);
    display: flex;
    /* height: 70vh; */
    border: 2px solid #00485c;
    border: 2px solid var(--secondary-color);
    border-radius: 25px;
    margin: 2vh 0;
    padding: 20px;
    min-height: 31rem;
    justify-content: center;
    align-items: center;
}

.drawbox>* {
    margin: auto;
}

