.message {
    display: flex;
    align-items: center;
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    border: 4px solid #00485c;
    border: 4px solid var(--secondary-color);
    border-radius: 25px;
    height: auto;
    padding: 0.625em;
    padding: var(--spacing_small);
    color: rgb(238, 238, 238);
    color: var(--light-color);
    /* transform: rotate(-10deg); */
    box-shadow: 7px 7px 30px 0px rgba(0, 0, 0, 0.41);
    /* transition: ease 1s; */
    font-size: 1rem;
    max-width: 935px;
}

button.button-back {
    display: block;
    margin: 1em auto !important;
}

.text-input {
    width: 30vw;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 700;
    -webkit-appearance: textfield;
    display: inline-block;
    padding: 15px 20px 15px 20px;
    border-style: solid none solid solid;
    border-width: 2px;
    border-color: #00485c transparent #00485c #00485c;
    border-color: var(--secondary-color) transparent var(--secondary-color) var(--secondary-color);
    border-radius: 25px 25px 25px 25px;
    box-shadow: inset 0 0 0 0 #fff;
}

.text-input:focus {
    outline: 0;
    border-color: rgb(139, 9, 199);
    border-color: var(--primary-color);
}

#back-button {

    /* margin-left: -27px; */
}

#back-button:hover {
    /* margin-left: calc(-1*var(--margin-main)); */
    background-color: #00485c;
    background-color: var(--secondary-color);
    border-color: rgb(139, 9, 199);
    border-color: var(--primary-color);
    color: rgb(238, 238, 238);
    color: var(--light-color);
}

.button {
    display: inline-block;
    padding: 14px 40px 12px;
    border: 2px solid #00485c;
    border: 2px solid var(--secondary-color);
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    transition: background-color 180ms ease, color 180ms ease, border-color 180ms ease;
    font-family: sans-serif;
    color: rgb(238, 238, 238);
    color: var(--light-color);
    border-radius: 25px;
    font-size: 18px;
    line-height: 26px;
    font-weight: 700;
    outline: 0;
    transition: ease 0.2s;
    -webkit-tap-highlight-color: #005baa;
}


.button-right {
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    /* border-right: 2px solid var(--secondary-color); */
    border-radius: 100px 100px 100px 100px;
    margin-bottom: 0.9375rem;
    margin-bottom: var(--margin-main);
}

.button-middle {
    background-color: mediumpurple;
}

.button:hover {
    border: 2px solid #00485c;
    border: 2px solid var(--secondary-color);
    background-color: rgb(238, 238, 238);
    background-color: var(--light-color);
    color: #00485c;
    color: var(--secondary-color);
}

.button:active {
    outline: 0;
    border-color: #00485c;
    border-color: var(--secondary-color);
    background-color: #00485c;
    background-color: var(--secondary-color);
    color: rgb(238, 238, 238);
    color: var(--light-color);
}

.button[disabled] {
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    color: rgb(238, 238, 238);
    color: var(--light-color);
    border: 2px solid #00485c;
    border: 2px solid var(--secondary-color);
    cursor: not-allowed;
}

.button[disabled]:hover,
.button[disabled]:active {
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    cursor: not-allowed;
    color: rgb(238, 238, 238);
    color: var(--light-color);
}

#start-button {
    background-color: rgb(139, 9, 199);
    background-color: var(--primary-color);
    /* padding: var(--spacing_small); */
    border-radius: 25px;
    color: rgb(238, 238, 238);
    color: var(--light-color);
}

@media screen and (max-width: 750px) {
    .message {}

    .drawbox {
        min-height: 50vh;
        height: 100%;
    }

    .message>h2 {
        font-size: 0.9em;
    }

    #start-button {
        width: 100px;
    }

    #back-button:hover,
    #back-button:active {
        /* margin-left: -4vw; */
    }

    .text-input {
        width: 100%;
        border: 2px solid rgb(90, 0, 192);
        border: 2px solid var(--button-color);
        border-radius: 25px;
    }

    form {
        padding: 0 0.5em;
    }

    form>.button {
        width: 100%;
        border: 2px solid rgb(90, 0, 192);
        border: 2px solid var(--button-color);
        padding: 10px;
        margin: 0.25em 0;
        border-radius: 10px;
    }

    form>* {
        border: 2px solid #76dbd1;
        border-radius: 10px;
    }

    .button[active]:hover {
        border: 2px solid white;
        margin-top: 2px;
    }

    path,
    line,
    ellipse {
        stroke-width: 2;
    }
}


/* SVG */
path,
line,
ellipse {
    fill: none;
    stroke: #000;
    vector-effect: non-scaling-stroke;
    stroke-width: 3;
}
